/* Container styles */
.signIn-container {
    min-height: 92vh;
    width: 100%;
    display: flex;
  }

  .signIn-form-container {
    width: 100%;
    background-color: #000;
    padding: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  @media (min-width: 1024px) {
    .signIn-form-container {
      width: 100%;
    }
  }
  
  .signIn-form-wrapper { 
    width: 100%;
    max-width: 25rem;
    padding: 2.5rem;
    border-radius: 5%;
    background-color: #43434339;
  }
  
  .signIn-logo-container { 
    display: flex;
    justify-content: center;
    margin-bottom: 3rem;
  }
  
  .signIn-logo-text { 
    color: white;
    font-size: 1.875rem;
    font-weight: 700;
  }
  
  /* Form styles */
  .signIn-auth-form { 
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .signIn-name-fields { 
    display: flex;
    gap: 1rem;
  }
  
  .signIn-form-group { 
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    flex: 1;
  }
  
  .signIn-form-group label { 
    text-align: left;
    color: #94a3b8;
    font-size: 0.875rem;
  }
  
  .signIn-form-input { 
    width: calc(100% - 2rem);
    padding: 0.75rem 1rem;
    border-radius: 0.5rem;
    background-color: transparent;
    color: white;
    border: 1.7px solid #25303e;
    outline: none;
    transition: all 0.2s ease;
  }
  
  .signIn-form-input:focus { 
    border-color: #3b82f6;
  }
  
  .signIn-submit-button { 
    width: 100%;
    padding: 0.75rem;
    border-radius: 0.5rem;
    background-color: #3b82f6;
    color: white;
    font-weight: 600;
    transition: background-color 0.2s ease;
    border: none;
    cursor: pointer;
  }
  
  .signIn-submit-button:hover { 
    background-color: #2563eb;
  }
  
  .signIn-additional-options { 
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .signIn-secondary-button { 
    width: 100%;
    padding: 0.75rem 0rem;
    border-radius: 0.5rem;
    border: 1px solid #334155;
    background: transparent;
    color: #94a3b8;
    font-size: 0.875rem;
    transition: all 0.2s ease;
    cursor: pointer;
    text-align: left;
    justify-content: space-between;
    display: flex;
    align-items: center;
    position: relative;
    text-decoration: none;
    gap: 0.5rem;
  }
  
  .signIn-secondary-button:hover { 
    background-color: #1a1d20;
  }

  .signIn-secondary-button > span {
    white-space: nowrap;
  }

  .signIn-secondary-button > :first-child {
    padding: 0 0.5rem 0 1rem;
    margin-top: 1.7px;
  }

  .signIn-secondary-button > :last-child {
    padding-right: 1rem;
  }
  
@media (max-width: 480px) {
  .signIn-secondary-button {
    font-size: 0.75rem;
    padding: 0.5rem 0;
  }

  .signIn-secondary-button > :first-child {
    padding: 0 0.25rem 0 0.5rem;
  }

  .signIn-secondary-button > :last-child {
    padding-right: 0.5rem;
  }
}

  /* Animation for form transitions */
  .signIn-auth-form { 
    animation: fadeIn 0.3s ease;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* Error states */
  .signIn-form-input.error { 
    border-color: #ef4444;
  }
  
  .signIn-error-message { 
    color: #ef4444;
    font-size: 0.875rem;
    margin-top: 0.25rem;
  }

  ::placeholder{
    color: rgba(255, 255, 255, 0.3);
  }

